import React, { Component } from "react"; // React Component  
import { render } from "react-dom"; // render  
import HomePage from "./HomePage";

export default class App extends Component {
  // App   Component  
  constructor(props) {
    // constructor   props  
    super(props); //    
  }

  render() {
    // render  
    return (
      <div className="center">
        <HomePage />
      </div>
    ); // h1    
  }
}

const appDiv = document.getElementById("app"); // appDiv  id "app"  
render(<App />, appDiv); // App  appDiv 
